import React, { Component } from 'react';
import './ShopCar.scss';
import { Icon } from "antd";
export default class ShopCar extends Component {
    render() {
        return (
            <div className="shopCar">
                <div className="header">
                    <Icon type="shopping-cart" />
                    <span>购物车</span>
                    <Icon onClick={() => this.props.closeClick()} style={{ float: "right", marginRight: 10, cursor: "pointer", fontSize: 25, marginTop: 5 }} type="close" />
                </div>
                <div>shopcar detail</div>
            </div>
        )
    }
}


