import React, { Component } from 'react';
import { Icon, Badge } from 'antd';
import './SideBar.scss';
import ShopCarContainer from '../ShopCarContainer/ShopCarContainer';
import UserInfo from "../UserInfo/UserInfo";
import User from '../User/User';
import ShopCar from '../ShopCar/ShopCar';
let userImg = require('../../static/user.png');
let shopcarImg = require('../../static/shopcar.png');
let userImgWhite = require('../../static/userWhite.png');
let shopcarImgWhite = require('../../static/shopcarWhite.png');

export default class SideBar extends Component {
    state = {
        translateX: 264,
        userImg: userImgWhite,
        shopcarImg: shopcarImg,
        sideBarUserBgColor: "#c81623",
        sideBarShopcarBgColor: "#666666",
        clicked: 'user'/* user|shopcar */,
        isOpen:false,
        Comp:User/* ShopCar|User|UserInfo */
    }
    userClick() {
        this.setState(state=>({ 
            clicked: "user" ,
            userImg:userImgWhite,
            shopcarImg:shopcarImg,
        }));
        
        if(!this.state.isOpen){
            this.setState({
                translateX:0
            });
        }
        let hasLogin = sessionStorage.getItem('hasLogin');
        this.setState({
            Comp:hasLogin ? UserInfo : User
        });
    }
    jumpToUserInfo(){
        this.setState({
            Comp:UserInfo
        });
    }
    shopcarClick() {
        this.setState(state=>({ 
            clicked: "shopcar",
            userImg:userImg,
            shopcarImg:shopcarImgWhite,
            Comp:ShopCar
        }));
        if(!this.state.isOpen){
            this.setState({
                translateX:0
            });
        }
    }
    closeClick(){
        this.setState({
            translateX:264
        });
    }
    render() {
        const { translateX, sideBarUserBgColor, sideBarShopcarBgColor, userImg, shopcarImg, clicked,Comp } = this.state;
        const { shopCount } = this.props;
        return (
            <div className="sideBar" style={{ transform: `translateX(${translateX}px)` }}>
                <div className="icon">
                    <div className="imgContainer">
                        <div
                            className="userImg"
                            style={{ background: (clicked === 'user' ? "#c81623" : "#666666") }}
                            onClick={() => this.userClick()}
                        >
                            <Badge dot>
                                <img src={userImg} />
                            </Badge>
                        </div>
                        <div
                            className="shopcarImg"
                            style={{ background: (clicked === 'shopcar' ? "#c81623" : "#666666") }}
                            onClick={() => this.shopcarClick()}
                        >
                            <Badge count={shopCount}>
                                <img src={shopcarImg} />
                            </Badge>
                        </div>
                    </div>
                </div>
                <div className="content1" >
                    <ShopCarContainer Comp={Comp} closeClick={()=>this.closeClick()} jumpToUserInfo={()=>this.jumpToUserInfo()}></ShopCarContainer>
                </div>

            </div>
        )
    }
    componentDidMount(){
        
    }
    static defaultProps = {
        shopCount: 0
    }
}


