import React, { Component } from 'react';
import './App.scss';
import ShopCenter from "./components/shopCenter/shopCenter"
import SideBar from './components/SideBar/SideBar';
export default class App extends Component {
  state = {
    shopCount: 0
  }
  addShopCount() {
    this.setState(state => ({ shopCount: state.shopCount + 1 }));
  }
  render() {
    return (
      <div className="app">
        {/* <ShopCenter onClick={() => this.addShopCount()}></ShopCenter> */}
        <SideBar shopCount={this.state.shopCount}></SideBar>
      </div>
    )
  }
}







