import React, { Component } from 'react';
import {Icon} from "antd";
import './GoodsItem.css';
var imgURL = require('../../static/1.jpg');
export default class GoodsItem extends Component {
    render() {
        const { imgURL, singlePrice, brief,description } = this.props;
        return (
            <div className="GoodsItem">
                <div className="GoodsItemTop">
                    <div className="GoodsItemImg">
                        <img src={imgURL} style={{ width: "100%", height: "100%" }} />
                    </div>
                    <div className="GoodsItemBrief">
                        <div className="GoodsItemBriefTop">
                            <div>{brief}</div>
                        </div>
                        <div className="GoodsItemBriefBottom">
                            <div className="GoodsItemBriefBottomD">{description}</div>
                        </div>
                    </div>
                </div>
                <div className="GoodsItemBottom" onClick={()=>this.props.onClick()}>
                    <div className="GoodsItemBottomLeft">
                        价格：¥ {singlePrice}
                    </div>
                    <div className="GoodsItemBottomRight">
                    <Icon type="shopping-cart" style={{fontSize:25,position: "relative",top: 4}} /> 加入购物车
                    </div>
                </div>

            </div>
        )
    }
    static defaultProps = {
        imgURL: imgURL,
        singlePrice: 22,
        brief: "福临门玉米清香调和油5L 中粮出品 新老包装随机发",
        description:"5L",
        onClick(){console.log('click',this)}
    }
}


