import React, { Component } from 'react';
export default class ShopCarContainer extends Component{
    render(){
        const Comp = this.props.Comp;
        return <Comp closeClick={()=>this.props.closeClick()} jumpToUserInfo={()=>this.props.jumpToUserInfo()}></Comp>
    }
}


