import React, { Component } from 'react';
import {Icon} from 'antd';
import './UserInfo.scss';
export default class User extends Component {
    render(){
        return(
            <div className="userInfo">
                <div className="header">
                    <Icon type="user" />
                    <span>万家会员信息</span>
                    <Icon onClick={() => this.props.closeClick()} style={{ float: "right", marginRight: 10, cursor: "pointer", fontSize: 25, marginTop: 5 }} type="close" />
                </div>
                <div>userinfo detail</div>
            </div>
        )
    }
    componentDidMount(){
        console.log('ajax请求用户信息数据');
    }
}


