import React, { Component } from 'react';
import './Divider.css';
export default class Divider extends Component{
    render(){
        const {title} = this.props;
        return(
            <div>
                <div className="sc-header">
                    <h3 className="sc-title">{title}</h3>
                </div>
            </div>
        )
    }
    static defaultProps = {
        title:"商品列表"
    }
}


