import React, { Component } from 'react';
import './User.scss';
import { Icon, Modal, Button, Tabs, Form, Input, Checkbox, message } from "antd";
import AccontLogIn from "../AccontDetail/AccontDetail";

let qrcode = require('../../static/ma.png');
let sampleImg = require('../../static/weixin.png');
const TabPane = Tabs.TabPane;
const FormItem = Form.Item;
class User extends Component {
    state = {
        visible: false,
        loading: false,
        registerVisiable: false,
        registLoading:false
    }
    func() {
        this.setState({ visible: true });
    }
    handleOk() {
        this.setState({ loading: true });
        setTimeout(() => {
            this.setState({ visible: false, loading: false });
        }, 1000);
    }
    handleCancel() {
        console.log('cancel');
        this.setState({ visible: false });
    }
    handleSubmit() {
        let _this = this;
        var obj = this.props.form.getFieldsValue(["userName", "password"]);
        if (obj.userName && obj.password) {
            this.setState({ loading: true });
            setTimeout(() => {
                sessionStorage.setItem('hasLogin', 'hasLogin');
                this.setState({ visible: false, loading: false });
                message.success('登陆成功');
                this.props.form.resetFields();
                _this.props.jumpToUserInfo();
            }, 1000);
        }
    }
    registModal() {
        this.setState({ registerVisiable: true });
    }
    registerOk() {
        this.setState({ registerVisiable: false });
    }
    registerCancel() {
        this.setState({ registerVisiable: false });
    }
    registSubmit(){
        var _this = this;
        var obj = this.props.form.getFieldsValue(["passwordRegist", "userNameRegist"]);
        if (obj.passwordRegist && obj.userNameRegist) {
            this.setState({registLoading:true});
            setTimeout(() => {
                sessionStorage.setItem('hasLogin', 'hasLogin');
                this.setState({ registerVisiable: false,registLoading:true });
                message.success('注册成功');
                this.props.form.resetFields();
                _this.props.jumpToUserInfo();
            }, 1000);
        }
    }
    render() {
        const { loading } = this.state;
        const { getFieldDecorator } = this.props.form;
        return (
            <div className="user1">
                <div className="header">
                    <Icon type="user" />
                    <span>万家会员登陆</span>
                    <Icon onClick={() => this.props.closeClick()} style={{ float: "right", marginRight: 10, cursor: "pointer", fontSize: 25, marginTop: 5 }} type="close" />
                </div>
                <div className="userContent">
                    <Button onClick={() => this.func()}>登陆</Button>
                    <Button onClick={() => this.registModal()}>注册</Button>
                </div>
                <Modal
                    className="logIn"
                    title="登陆"
                    visible={this.state.visible}
                    onOk={() => this.handleOk()}
                    onCancel={() => this.handleCancel()}
                    footer={null}
                >
                    <Tabs tabBarStyle={{ textAlign: "center" }}>
                        <TabPane tab="扫码登录" key="1">
                            <div className="scanLogIn">
                                <div className="qrcode">
                                    <img src={qrcode} />
                                </div>
                                <div className="sample">
                                    <img src={sampleImg} />
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab="账户登录" key="2">
                            <div className="accontLogIn">
                                <Form onSubmit={() => this.handleSubmit()} className="login-form">
                                    <FormItem>
                                        {getFieldDecorator('userName', {
                                            rules: [{ required: true, message: '请输入用户名' }],
                                        })(
                                            <Input prefix={<Icon type="user" style={{ fontSize: 13 }} />} placeholder="请输入用户名" />
                                            )}
                                    </FormItem>
                                    <FormItem>
                                        {getFieldDecorator('password', {
                                            rules: [{ required: true, message: '请输入密码' }],
                                        })(
                                            <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />} type="password" placeholder="请输入密码" />
                                            )}
                                    </FormItem>
                                    <div className="loginButtom">
                                        <Button type="primary" size="default" loading={loading} onClick={() => this.handleSubmit()}>登陆</Button>
                                    </div>
                                </Form>
                            </div>
                        </TabPane>
                    </Tabs>
                </Modal>
                <Modal
                    className="register"
                    title="注册"
                    visible={this.state.registerVisiable}
                    onOk={() => this.registerOk()}
                    onCancel={() => this.registerCancel()}
                    footer={null}
                >
                    <div className="accontRegist">
                        <Form onSubmit={() => this.handleSubmit()} className="login-form">
                            <FormItem>
                                {getFieldDecorator('userNameRegist', {
                                    rules: [{ required: true, message: '请输入用户名' }],
                                })(
                                    <Input prefix={<Icon type="user" style={{ fontSize: 13 }} />} placeholder="请输入用户名" />
                                    )}
                            </FormItem>
                            <FormItem>
                                {getFieldDecorator('passwordRegist', {
                                    rules: [{ required: true, message: '请输入密码' }],
                                })(
                                    <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />} type="password" placeholder="请输入密码" />
                                    )}
                            </FormItem>
                            <div className="registButtom">
                                <Button loading={this.state.registLoading} onClick={()=>this.registSubmit()} size="default" style={{ marginRight: 20, cursor: "pointer" }}>立即注册</Button>
                            </div>
                        </Form>
                    </div>
                </Modal>
            </div>
        )
    }
    componentDidMount() {
        console.log('===========');
    }
}

export default Form.create()(User);


