import React, { Component } from 'react';
import { message, Spin } from 'antd';
import Divider from '../Divider/Divider';
import GoodsItem from '../GoodsItem/GoodsItem';

import './shopCenter.scss';

export default class ShopCenter extends Component {
    state = {
        goodsList: [],
        loading: false
    }
    func() {
        message.loading('This is a message of success');
    }

    render() {
        const { onClick } = this.props;
        function goodsList(goodsList) {
            return goodsList.map((item, index) => <GoodsItem {...item} onClick={onClick} key={index}></GoodsItem>)
        }
        return (
            <Spin spinning={this.state.loading}>
                <div className="shopCenter">
                    <Divider title="商品列表"></Divider>
                    <div className="goodListContainer">
                        {goodsList(this.state.goodsList)}
                    </div>
                </div>
            </Spin>
        )
    }
    componentDidMount() {
        let _this = this;
        this.setState({ loading: true });
        fetch('http://localhost:9999/goodList')
            .then(res => {
                this.setState({ loading: false })
                if (res.status == 200) {
                    return res.json();

                }
            })
            .then(res => {
                if (res.result == 1) {
                    var goodsList = res.data;
                    _this.setState({ goodsList });
                }
            })
            .catch(res => {
                console.log('catch--');
            })
    }
}


